import React from "react";
import { Container, Title, Wrapper, Card } from "./styles";
import bairroProject from "../../assets/bairro.gif";
import neumorphismProject from "../../assets/neumorphism.gif";
import spotifyProject from "../../assets/spotify.gif";
import mealProject from "../../assets/meal.png";
import movieProject from "../../assets/movieCenter.png";

const Works = () => {
  const works = [
    {
      id: 1,
      name: "Bairro em Foco",
      link: "https://www.bairroemfocoapp.com.br/",
      image: bairroProject,
      desc: `Participação no projeto feito com React e cujo a finalidade é
      divulgar comerciantes locais de serviços/produtos essênciais no
      período de quarentena do covid19.`,
    },
    {
      id: 2,
      name: "UX Neumorphism",
      link: "https://github.com/flavioms/neumorphism-example",
      image: neumorphismProject,
      desc: `A página foi feita com o objetivo de testar a nova tendência de
      layout. Também foi aplicado a técnica de dark mode, salvando a
      escolha do usuario no localstorage através de um hook
      customizado.`,
    },
    {
      id: 3,
      name: "Player Spotify",
      link: "https://github.com/flavioms/Player-Spotify",
      image: spotifyProject,
      desc: `Aplicativo feito com React para buscar e reproduzir músicas
      utilizando a API do spotify mas com uma interface mais objetiva.
      Todos os componentes incluindo os de controle foram criados
      manualmente.`,
    },
    {
      id: 4,
      name: "Random Meal",
      link: "https://flavioms-random-meal.netlify.app",
      image: mealProject,
      desc: `Aplicativo feito com React para gerar receitas aleatórias e salvar uma lista de favoritas`,
    },
    {
      id: 5,
      name: "Movie Center",
      link: "https://moviecenter-416af.web.app/",
      image: movieProject,
      desc: `Aplicativo feito com React onde é possivel fazer autenticação com redes sociais, buscar filmes e adicionar comentários nos mesmos.`,
    },
  ];
  return (
    <Container>
      <Title id="works">Trabalhos</Title>
      <Wrapper>
        {works.map((work) => (
          <Card key={work.id}>
            <div className="front">
              <img src={work.image} alt="" />
            </div>
            <div className="back">
              <a className="back-content" href={work.link} target="_blank">
                <h2>{work.name}</h2>
                <span>{work.desc}</span>
              </a>
            </div>
          </Card>
        ))}
      </Wrapper>
    </Container>
  );
};

export default Works;
