import React from "react";

import iconTs from "../../assets/iconTs.svg";
import {
  Container,
  Title,
  Wrapper,
  Skill,
  IconReact,
  IconCss,
  IconHtml,
  IconJs,
  IconNode,
  IconTs,
  IconMongodb,
  IconMsqlServer,
  IconMysql,
  IconRedis,
} from "./styles";

const Skills = () => {
  const skills = [
    { id: 1, name: "ReactJS & React Native", icon: <IconReact /> },
    { id: 2, name: "NodeJS", icon: <IconNode /> },
    { id: 3, name: "Javascript ES6+", icon: <IconJs /> },
    { id: 4, name: "Typescript", icon: <IconTs src={iconTs} /> },
    { id: 5, name: "Html5", icon: <IconHtml /> },
    { id: 6, name: "CSS3", icon: <IconCss /> },
    { id: 7, name: "MSSQL", icon: <IconMsqlServer /> },
    { id: 8, name: "MySQL", icon: <IconMysql /> },
    { id: 9, name: "MongoDB", icon: <IconMongodb /> },
    { id: 10, name: "Redis", icon: <IconRedis /> },
  ];
  return (
    <Container>
      <Title id="skills">Skills</Title>

      <Wrapper>
        {skills.map((skill) => (
          <Skill
            key={skill.id}
            whileHover={{
              y: [0, 10, 0, -10, 0],
            }}
            transition={{
              duration: 1,
              loop: 2,
            }}
          >
            {skill.icon}
            <p>{skill.name}</p>
          </Skill>
        ))}
      </Wrapper>
    </Container>
  );
};

export default Skills;
